.footer {
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  font-size: 1.2rem;
}
  
.footer-content {
  padding: 20px;
  border-radius: 10px;
  z-index: 1; /* Ensures content is above the background */
}
  
.footer-text  a:hover {
  text-decoration: underline;
}

.language-text {
  font-size: 1.5rem;
  margin-top: 10px;
}